.pagetop {
  height: 50px;
  width: 50px;
  position: fixed;
  right: 30px;
  bottom: 50px;
  background: #fff;
  border: solid 2px #000;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  cursor: pointer;
  transition: all .3s;
  opacity: 0; /* デフォルトでは表示を消します */
  visibility: hidden; /* デフォルトでは表示を消します */
}
.pagetop_arrow {
  display: block;
  height: 10px;
  width: 10px;
  border-top: 3px solid #000;
  border-right: 3px solid #000;
  transform: translateY(20%) rotate(-45deg);
}
/* TOPボタン出現後のスタイル */
.pagetop.fadeIn {
  opacity: 0.5;
  visibility: inherit;
}
.pagetop:hover {
  opacity: 1;
}

.pager {
  clear: both;
  margin: 2em 0;
  border-top: 1px solid #c8c8c8;
  border-bottom: 1px solid #c8c8c8;
  p {
    line-height: 1.5em;
    margin: 0.5em 0;
  }
  p.next {
    float: right;
  }
  p.prev {
    float: left;
  }
}

details {
  margin-bottom: 1em;
}
details:hover {
  cursor: pointer;
}
details summary {
  font-weight: bold;
  h3, h4, h5, h6 {
    padding: 0;
